html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  color: #171717;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Lexend Deca, sans-serif;
}

.app {
  text-align: center;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 1rem;
  display: flex;
}

.app-input {
  color: #f08080;
  cursor: pointer;
  background-color: #eee;
  border: none;
  border-radius: 1rem;
  width: 13em;
  margin-bottom: 35px;
  padding: 1rem;
  font-size: 1rem;
  box-shadow: 0 .4rem #dfd9d9;
}

.app-input:focus {
  outline-color: #f08080;
}

.app-list {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 5px;
  list-style: outside;
  display: flex;
}

.app-cname {
  margin-bottom: 20px;
  font-size: 24px;
}

.app-box {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 40px;
  max-width: 100%;
  display: flex;
}

.app-cinfo {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  font-size: 18px;
  display: flex;
}

.app-subtitle-info {
  color: #404040;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.app-clanguages {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.clanguages-subtitle {
  color: #404040;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.app-cimage img {
  border-radius: 8px;
  max-width: 100%;
  height: auto;
}

@media (width <= 1024px) {
  .app-box {
    flex-direction: row;
    gap: 20px;
  }

  .app-cinfo {
    font-size: 16px;
  }

  .app-cname {
    font-size: 22px;
  }

  .app-input {
    width: 70%;
    max-width: 400px;
  }

  .app-cimage img {
    width: 300px;
  }
}

@media (width <= 767px) {
  .app {
    padding: 32px 16px;
  }

  .app-box {
    flex-direction: column;
    gap: 20px;
  }

  .app-input {
    width: 100%;
    max-width: 350px;
    font-size: 16px;
  }

  .app-cname {
    font-size: 21px;
  }

  .app-cinfo {
    gap: 15px;
    font-size: 16px;
  }

  .app-cimage img {
    width: 90%;
    height: auto;
  }
}

@media (width <= 480px) {
  .app-cname {
    font-size: 18px;
  }

  .app-input, .app-cinfo {
    font-size: 16px;
  }

  .app-cimage img {
    width: 100%;
  }
}
